//Footer


footer {
    .nanagames{
        font-size: 2.875rem;
    }
    a {
        margin-bottom:1rem;
        color: var(--main-color-lighter);
        text-decoration: none;
        font-family: $font-family-base;

        &:hover {
            color: white;
        }
    }
}

// Navbar

nav {
    a {
        text-transform: uppercase;
        letter-spacing: .5px;
        font-family: $fredoka-font-family;

        &:hover {
            img{
                opacity: 1;
            }
        }
    }
}

.nav-item {
    padding: 0rem .75rem;
}

.navbar {
    min-height: 78px;
}

.navbar-nav {
    img {
        height: 30px;
        opacity: .7;
        transition: all .3s ease;
    }
    .nav-link.active  {
        color:var(--main-color)!important;
        img {
            opacity: 1;
        }
    }
}